
.clipCounter{
  display: flex;
  align-items: center;
  &__icon{
    width: 20px;
    height: 20px;
    // background-color: #c3c3c3;
    background-color: #00bb33;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    .Icon{
      color: #fff;
      // font-size: 12px;
      top: 0;
    }
  }
  &__num{
    font-size: 0.8rem;
    line-height: 1;
    padding: 5px 7px;
    border: solid 1px #ccc;
    border-radius: 5px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: -10px;
      margin-top: -5px;
      border: 5px solid transparent;
      border-right: 5px solid #FFF;
      z-index: 2;
    }
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: -12px;
      margin-top: -6px;
      border: 6px solid transparent;
      border-right: 6px solid #ccc;
      z-index: 1;
    }
  }
}

.qaListContainer{
  @media all and (min-width: $breakpoint + 1){
    background-color: #fff;
    padding-bottom: 30px;
  }
}

//Q&Aリスト
.qaList{
  &__item{
    border: 1px solid #ccc;
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }
  &__itemInner{
    color: inherit;
    padding: 10px;
    display: flex;
    position: relative;
  }
  &__itemEyecatch{
    width: 100px;
    height: 100px;
    border-radius: 4px;
    background-size: cover;
    background-position: center;
  }
  &__textBlock{
    width: calc(100% - 100px);
    padding-left: 10px;
  }
  &__status{
    display: flex;
    margin-bottom: 8px;
  }
  &__itemCategory{
    display: inline-block;
    color: #fff;
    font-size: 11px;
    line-height: 1;
    padding: 5px 10px;
  }
  &__itemClip{
    margin-left: 10px;
  }
  &__date{
    font-size: 0.8rem;
    margin-bottom: 3px;
  }
  &__question{
    line-height: 1.3;
    font-size: 1.05rem;
    font-weight: 700;
    color: #53a7db;
    position: relative;
    padding-left: 1.3em;
    &::before{
      content: "Q:";
      position: absolute;
      left: 0;
      top: 0;
    }
  }
  &__questionDescription{
    display: none;
  }
  @media all and (min-width: $breakpoint + 1){
    &__item{
      border: none;
      border-bottom: 1px solid #ededed;
    }
    &__itemInner{
      color: inherit;
      padding: 30px 15px 30px 0;
      display: flex;
      position: relative;
      @include hover;
    }
    &__itemEyecatch{
      width: 45%;
      height: 0;
      padding-top: 60%*0.45;
      position: relative;
      border-radius: 0;
    }
    &__textBlock{
      width: 55%;
      padding-left: 25px;
    }
    &__itemCategory{
      position: absolute;
      top: 0;
      left: 0;
      padding: 7px 15px;
    }
    &__itemClip{
      position: absolute;
      bottom: 0;
      right: 10px;
      background-color: #fff;
      padding: 5px 10px;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
    }
    &__date{
      margin-bottom: 10px;
    }
    &__question{
      font-size: 20px;
      margin-bottom: 10px;
    }
    &__questionDescription{
      display: block;
    }
  }
}

//Q&A詳細
.qaArticle{
  background-color: #fff;
  position: relative;
  &__category{
    position: absolute;
    top: 0;
    left: 0;
    background-color: #5360db;
    color: #fff;
    font-size: 11px;
    line-height: 1;
    padding: 7px 15px;
  }
  &__eyecatchImg{
    width: 100%;
  }
  &__body{
    padding: 15px;
    position: relative;
  }
  &__clip{
    position: absolute;
    top: 0;
    right: 10px;
    transform: translateY(-100%);
    background-color: #fff;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    padding: 5px 10px;
  }

  &__date{
    font-size: 0.85rem;
    margin-bottom: 15px;
  }
  &__question{
    font-size: 1.15rem;
    line-height: 1.3;
    font-weight: bold;
    margin-bottom: 10px;
    color: #53a7db;
    position: relative;
    padding-left: 1.3em;
    &::before{
      content: 'Q:';
      position: absolute;
      left: 0;
      top: 0;
    }
  }
  &__viewAnswer{
    margin-top: 20px;
    text-align: center;
  }
  @media all and (min-width: $breakpoint + 1){
    &__category{
      font-size: 0.85rem;
      padding: 10px 20px;
    }
    &__body{
      padding: 30px;
    }
    &__question{
      font-size: 20px;
      margin-bottom: 15px;
    }
  }
}

//ランキング
.ranking{
  border: 3px solid #bcbaba;
  background-color: #fff;
  &__select{
    padding: 10px;
    border-bottom: 1px solid #ccc;
  }
  &__list{
    padding: 10px;
  }
  &__listItem{
    counter-increment: rank;
    &::before{
      content: counter(rank) "位";
    }
    a{
      position: relative;
      padding-left: 1.3em;

      &::before{
        content: 'Q:';
        line-height: 1;
        position: absolute;
        display: block;
        top: 0;
        left: 0;
      }
    }
  }
  &__listItemCategory{
    font-size: 0.8rem;
    margin-left: 5px;
  }
}
